import React, {useEffect, useState} from 'react';
import {getCurrentUserInfo} from "../../services/redux/reducers/user/user-reducer";
import {useDispatch, useSelector} from '../../hooks';
import './dashboard.scss'
import MapLayers from "./map-component/map";
import D3Project from "./tree-component/d3-project";
import CardHeader from "../../ui-components/card-header/card-header";
import { getProjects } from '../../services/redux/reducers/project/projects.slice';
import { CreateProjectResponse } from '../../typings/api';
import { ActiveNodesType, MapCoordsType, ProjectData, TreeNodeType } from '../../typings';
import ProjectExplorer from '../../ui-components/project/explore/ProjectExplorer.component';
import { addNodeToTree, loadKnowledgeTree } from '../../services/redux/reducers/project/knowledge-tree.slice';
import { loadProjectNodeTypes } from '../../services/redux/reducers/project/project-node-types.slice';
import { newTreeNode } from '../../utils';
import FavBlock from '../../ui-components/favorite-block/FavBlock.component';
import InvitationContainer from "./invitation-panel";
import {RootState} from "../../services/redux/store";
import { list, listViewColumns } from '../../nocodb';
import ProjectDataTable from '../../ui-components/project/explore/ProjectDataTable.component';
import {ProjectViewChange} from "../../services/redux/reducers/auth/auth.slice";
import { useTranslation } from 'react-i18next';
import Progress from '../../ui-components/commons/progress/Progress.component';
import CirclePlusIcon from '../../ui-components/icons/CirclePlus.icon';
import { index } from 'd3';
import { EditableCell } from '../../ui-components/commons/table';


const Dashboard = () => {
    const dispatch = useDispatch();
    const {projects: storeProjects, knowledgeTree, projectNodeTypes} = useSelector(state => state);
    const tab = useSelector<string>((state) => state.pages.selected_tab);
    const [activeNodes, setActiveNodes] = useState<any>({});
    const [active, setActive] = useState<TreeNodeType | null>();
    const [treeData, setTreeData] = useState<any>(newTreeNode);
    const [projects, setProjects] = useState<Array<ProjectData | CreateProjectResponse>>();
    const [exploreProjectId, setExploreProjectId] = useState<any>();
    const [mapCoords, setMapCoords] = useState<MapCoordsType>();
    const [newColumnClick, setNewColumnClick] = useState(false);
    const [newColumnName, setNewColumnName] = useState('');

    const { t } = useTranslation();

    const setProjectView = (value:boolean) => {
        if (knowledgeTree.payload)
            dispatch(ProjectViewChange({is_active: value, name: knowledgeTree.payload.name}))
    }

    useEffect(() => {
        dispatch(getCurrentUserInfo());
        dispatch(getProjects());
        dispatch(loadProjectNodeTypes())
    }, []);

    useEffect(() => {
        if (!storeProjects.isLoading && storeProjects.payload) {
            setProjects(storeProjects.payload);
            if (storeProjects?.payload?.length > 0) {
                setExploreProjectId((prev: any) => prev ? prev : storeProjects?.payload[0]?.id)
            }
        }
    }, [storeProjects]);

    useEffect(() => {
        if (exploreProjectId) {
            if (exploreProjectId !== "new-project")
                dispatch(loadKnowledgeTree(exploreProjectId));
            else setTreeData(newTreeNode);
        }
        console.log({ exploreProjectId });
    }, [exploreProjectId]);

    useEffect(() => {
        if (!knowledgeTree.isLoading && knowledgeTree.payload) {
            setTreeData(knowledgeTree.payload);
        }
    }, [knowledgeTree])

    useEffect(() => {
        if (typeof active !== 'undefined' && active !== null) {
            let node = active;
            let parentIds: ActiveNodesType = {};
            while (node !== null && node !== undefined) {
                if (node?.data?.attributes?.id && node?.parent?.data?.attributes?.id) {
                    parentIds = {
                        ...parentIds,
                        [node?.parent?.data?.attributes?.id as string]: node.parent,
                        [node?.data?.attributes?.id as string]: node,
                    };
                }
                node = node?.parent as TreeNodeType;
            }
            setActiveNodes(parentIds);
            setProjectView(true);
        } else {
            setProjectView(false);
        }
    }, [active]);
    const handleAddNewNode = ({ id, node}: {id: any; node: any;}) => {
        dispatch(addNodeToTree({id, node: {
            description: node?.attributes?.description,
            name: node?.name,
            typeId: projectNodeTypes?.payload?.find(n => n.label === node?.attributes?.nodeType)?.id }
        }));
    }
    const handleAddNewColumn = (name: string) => {
        if (name?.trim()) {
            projects?.forEach(async (p) => {
                await dispatch(addNodeToTree({id: p?.id, node: {
                    description: '',
                    name,
                    typeId: projectNodeTypes?.payload?.find(n => n.label === 'equipe')?.id }
                }));
            });
            setNewColumnName('');
        }
        setNewColumnClick(false);
    }
    const handleBlurNewColumn = (name: string) => {
        setNewColumnName(name);
        setNewColumnClick(false);
    }
    const getStatus = (p: any) => {
        const status = (p?.tree ? p?.tree?.attributes?.status : p?.attributes?.status) ?? 'todo';
        let color = '#ED6E40';
        switch(status) {
            case 'in-progress':
                color = '#F3BE3D';
                break;
            case 'planned':
                color = '#295EA7';
                break;
            case 'blocked':
                color = '#E33F1A';
                break;
            case 'todo':
                color = '#959595';
                break;
            case 'delivered':
            case 'deployed':
                color = '#29A74A';
                break;
            default:
                break;
        }
        return { status, color };
    }

    return (
        <>
            {tab === "Invitation" && (
                <InvitationContainer project={exploreProjectId}/>
            )}
            <div className={"dashboard-layout"}>
                <div className={"explorer"}>
                    <CardHeader title={"Explorer"} search={
                        <ProjectExplorer
                            active={active}
                            activeNodes={activeNodes}
                            setActive={setActive}
                            setActiveNodes={setActiveNodes}
                            projects={projects}
                            selectedProjectId={exploreProjectId}
                            setSelectedProjectId={setExploreProjectId}
                        />
                    }/>
                    <FavBlock favs={projectNodeTypes.payload ?? []}/>
                    <div className={"canvas-explorer"}>
                        <D3Project
                            active={active}
                            treeData={treeData}
                            activeNodes={activeNodes}
                            setActive={setActive}
                            setActiveNodes={setActiveNodes}
                            selectedProjectId={exploreProjectId}
                            mapCoords={mapCoords}
                        />
                    </div>
                </div>
                <div className={"project"}>
                    <CardHeader title={`Contextualiser (${active?.data?.name ? active?.data?.name : "Mes projets"})`}
                                search={"Carte"}/>
                    <div className={"map-container"}>
                        <MapLayers onPin={setMapCoords} pins={projects}/>
                    </div>
                    <ProjectDataTable data={projects as ProjectData[]} meta={{
                        name: {
                            displayName: t('project-name'),
                            render: (p, index) => (
                                <div style={{
                                    padding: '0 5px',
                                    background: '#fff',
                                    borderTopLeftRadius: index === 0 ? 5 : 0,
                                    borderTopRightRadius: index === 0 ? 5 : 0,
                                }}>{p.name}</div>
                            )
                        },
                        creator: {
                          displayName: t('project-manager'),
                          render: (p, index) => (
                            <div  style={{
                                    padding: '0 5px',
                                    background: '#fff',
                                    borderTopLeftRadius: index === 0 ? 5 : 0,
                                    borderTopRightRadius: index === 0 ? 5 : 0,
                                }}
                                title={`${p.creator.firstName ?? ''} ${p.creator.lastName ?? ''}`}
                            >
                                {`${p.creator.firstName ?? ''} ${p.creator.lastName ?? ''}`}
                            </div>
                          )
                        },
                        progression: {
                            displayName: t('progression'),
                            render: (p, index) => (
                                <div style={{
                                    display: 'flex',
                                    background: '#fff',
                                    borderTopLeftRadius: index === 0 ? 5 : 0,
                                    borderTopRightRadius: index === 0 ? 5 : 0,
                                }}>
                                    <Progress color="#ED6E40" progress={50} />
                                </div>
                            )
                        },
                        status: {
                            displayName: t('status'),
                            render: (p) => {
                                const st = getStatus(p);
                                return <div style={{ background: st.color, color: '#fff', padding: '0 5px' }}>{t(st.status)}</div>
                            }
                        },
                      }}
                      treeMeta={{
                        status: {
                            render: (pr, p) => {
                                const st = getStatus(p);
                                return <div style={{ background: st.color, color: '#fff', padding: '0 5px' }}>{t(st.status)}</div>
                            }
                        },
                        none: {
                            render: (p, node) => <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => handleAddNewNode({ id: p.id, node })} />
                        }
                      }}
                      newColumn={{
                        renderData: (index) => {
                            return <div style={{
                                    display: 'flex',
                                    background: '#fff',
                                    borderTopLeftRadius: index === 0 ? 5 : 0,
                                    borderTopRightRadius: index === 0 ? 5 : 0,
                            }}>
                                <span style={{ visibility: 'hidden' }}>h</span>
                            </div>
                        },
                        renderHeader: () => newColumnClick ? (
                            <EditableCell
                                autoFocus
                                style={{ textAlign: 'right' }}
                                onBlur={handleBlurNewColumn}
                                onEnter={handleAddNewColumn}
                                defaultValue={newColumnName}
                            />
                        ) : <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => setNewColumnClick(true)} />
                      }}
                      newRow={{
                        render: (d, index, len) => {
                            return (
                                <div style={{
                                    display: 'flex',
                                    background: '#fff',
                                    borderBottomLeftRadius: index === 0 ? 5 : 0,
                                    borderBottomRightRadius: index === len - 1 ? 5 : 0,
                            }}>
                                { index === 0 ? (
                                     <span style={{ padding: '0 10px' }} title="use the map to add new projects">
                                        <CirclePlusIcon style={{ height: 20, width: 20, marginTop: -5 }} />
                                     </span>
                                ) :  <span style={{ visibility: 'hidden' }}>h</span> }
                            </div>
                            )
                        }
                      }}
                    />
                </div>
            </div>
        </>
    )
}
export default Dashboard;